import React from "react";
// 
import {Link, useNavigate} from "react-router-dom"
// css
import "./css/index.css"
// antd
import "antd/dist/reset.css"
import {Input, Radio} from "antd"
// axios(POST)
import axios from "axios"
// 
import {Url_Head} from "../../Constant/constant1"
 
interface LoginPageProps{
}

interface LoginPageState{
    Identity: string, // 
    Account: string, // 
    Password: string, // 
}

/**
 * History(Nagitive)
 */

function LoginPage(props: LoginPageProps, state: LoginPageState){
    // ()
    const navigate = useNavigate()
    // state
    const [Identity, Set_Identity] = React.useState("")
    const [Account, Set_Account] = React.useState("")
    const [Password, Set_Password] = React.useState("")

    // 
    const Identity_url = { //  - url
        "": "/student-page/analyse",
        "": "/teacher-page/class-learning",
        "": "/administrator-page"
    }

    // 
    function Get_account(value: any){
        Set_Account(value.target.value)
    }
    // 
    function Get_password(value: any){
        Set_Password(value.target.value)
    }
    // 
    function Change_identity(value: any){
        Set_Identity(value.target.value.toString())
    }
    // 
    function Login_click(){
        // alert("")
        // // navigate("/student-page") // 
        // navigate("/student-page", { state: {form: "Aisa", numbers: 123}, replace: true}) // ( replace: truenice)
        // // window.location.href = "http://localhost:3000/student-page" // js

        /*  */
        let url = Url_Head + "database_login"
        axios({
            url: url,
            method: 'POST',
            data: {
                "identity": Identity,
                "account": Account,
                "password": Password
            }
        }).then(response => {
            let data_user = response.data
            if (data_user !== "") {
                alert(data_user.name + "")
                navigate((Identity_url as any)[data_user.identity], {state: data_user, replace: true})
            } else {
                confirm("")
            }
        })

        // /*  */
        // let user_data = {id: '191960028', name: '', account: '20211004050', identity: '', password: '123'}
        // alert(user_data.name + "")
        // // navigate("/student-page/analyse", {state: user_data, replace: true}) // 
        // navigate("/teacher-page", {state: user_data, replace: true})
    }
    // 
    function Enroll_click(){
        alert("")
    }

    return (
        <div className="LoginPage">
            {/*  */} 
            <div id="login-main-content">
                <div style={{float: 'left', position: 'relative', marginTop: 20, marginBottom: 0, fontSize: '24px', color: 'black', fontWeight: "700", textAlign: 'center', width: "100%"}}></div>
                <div className="login-item" style={{float: 'left'}}>
                    <div style={{float: 'left', width: '15%'}}></div>
                    <Input placeholder="" onChange={Get_account} style={{width: "85%", float: 'left'}}></Input>
                </div>
                <div className="login-item" style={{float: 'left'}}>
                    <div style={{float: 'left', width: '15%'}}></div>
                    <Input.Password placeholder="" onChange={Get_password} style={{width: "85%", float: 'left'}}></Input.Password>
                </div>
                <div className="login-item" style={{float: 'left'}}>
                    <Radio.Group onChange={Change_identity} value={Identity} style={{width: '100%'}}>
                        <Radio value={""} className="login-radio-item"></Radio>
                        <Radio value={""} className="login-radio-item"></Radio>
                        <Radio value={""} className="login-radio-item"></Radio>
                    </Radio.Group>
                </div>
                <div className="login-item login-button" style={{float: 'left', marginTop: 10}} onClick={Login_click}>
                    {/* <Link to="/student-page" style={{width: '100%'}}> */}
                        <div className="login-button-text" style={{width: '100%', textAlign: 'center' }}></div>
                    {/* </Link> */}
                </div>
                <div className="login-item login-button" style={{float: 'left', marginTop: 10}} onClick={Enroll_click}>
                    <Link to="/student-page" style={{width: '100%'}}>
                       <div className="login-button-text" style={{width: '100%', textAlign: 'center'}}></div> 
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default LoginPage